.main-app {
  min-height: 100vh;
  background-color: #f9fafb;
}

.main-app__header {
  background: white;
  border-bottom: 1px solid #e5e7eb;
  padding: 2rem;
}

.main-app__header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.main-app__title {
  font-size: 2rem;
  font-weight: bold;
  color: #1f2937;
  margin: 0;
}

.main-app__project-info {
  margin-top: 8px;
}

.main-app__project-name {
  font-size: 1.25rem;
  font-weight: 600;
  color: #667eea;
  margin: 0 0 4px 0;
}

.main-app__project-address {
  font-size: 0.9rem;
  color: #718096;
  margin: 0;
}

.main-app__user-info {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.main-app__home-btn {
  background-color: #3b82f6;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}

.main-app__home-btn:hover {
  background-color: #2563eb;
}

.main-app__user-name {
  font-size: 0.9rem;
  color: #6b7280;
  font-weight: 500;
}

.main-app__logout-btn {
  padding: 0.5rem 1rem;
  background: #ef4444;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}

.main-app__logout-btn:hover {
  background: #dc2626;
}

.main-app__steps {
  display: flex;
  justify-content: center;
  gap: 2rem;
  max-width: 600px;
  margin: 0 auto;
}

.main-app__step {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  opacity: 0.5;
  transition: opacity 0.3s;
}

.main-app__step--active {
  opacity: 1;
}

.main-app__step-number {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background-color: #e5e7eb;
  color: #6b7280;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  transition: all 0.3s;
}

.main-app__step--active .main-app__step-number {
  background-color: #10b981;
  color: white;
}

.main-app__step-label {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}

.main-app__content {
  padding: 2rem;
  max-width: none;
  margin: 0 auto;
}

.main-app__error {
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 0.5rem;
  padding: 1rem;
  margin-bottom: 2rem;
  text-align: center;
}

.main-app__error p {
  color: #dc2626;
  margin: 0 0 1rem 0;
}

.main-app__reset-button {
  background-color: #dc2626;
  color: white;
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-size: 0.875rem;
}

.main-app__reset-button:hover {
  background-color: #b91c1c;
}

.main-app__navigation {
  display: flex;
  justify-content: center;
  gap: 1rem;
  padding: 2rem;
  background: white;
  border-top: 1px solid #e5e7eb;
}

.main-app__nav-button {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.5rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.main-app__nav-button--primary {
  background-color: #10b981;
  color: white;
}

.main-app__nav-button--primary:hover {
  background-color: #059669;
}

.main-app__nav-button--secondary {
  background-color: #f3f4f6;
  color: #374151;
  border: 1px solid #d1d5db;
}

.main-app__nav-button--secondary:hover {
  background-color: #e5e7eb;
}

/* 네비게이션 정보 스타일 */
.main-app__nav-info {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #f7fafc 0%, #edf2f7 100%);
  border-radius: 12px;
  border: 1px solid #e2e8f0;
}

.main-app__nav-text {
  color: #4a5568;
  font-weight: 500;
  font-size: 0.95rem;
  text-align: center;
}

/* 전처리된 프로젝트 목록 스타일 */
.processed-projects-section {
  margin-top: 3rem;
  padding: 2rem;
  background: white;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
}

.processed-projects-section h3 {
  color: #374151;
  margin-bottom: 1.5rem;
  font-size: 1.25rem;
  font-weight: 600;
}

.processed-projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1rem;
}

.processed-project-card {
  padding: 1.5rem;
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.processed-project-card:hover {
  background: #eff6ff;
  border-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.15);
}

.processed-project-card h4 {
  color: #374151;
  margin: 0 0 0.5rem 0;
  font-size: 1rem;
  font-weight: 600;
}

.processed-project-card p {
  color: #6b7280;
  margin: 0.25rem 0;
  font-size: 0.875rem;
}